<template>
  <div id="map">
    <!--------------------- sidebar html ------------------->
    <div v-if="isSidebarOpen" class="sidebar"> 
      <div class="menuBtn">
        <div @click="menuContent('news')" :class="{'menu': menuPage === 'news'}">NEWS</div>
        <div @click="menuContent('review')" :class="{'menu': menuPage === 'review'}">REVIEW</div>
      </div>
      
      <!-- Îâ¥Ïä§ page -->
      <div class="news" v-if=" menuPage === 'news'"> 
        <div class="newsbar-header">
          <div class="search-icon" v-text="searchText"></div>
        </div>

        <div ref="chatArea" class="newsbar-body">
          <div class="newsbar-contain" v-for="(item, index) in searchList" :key="index" >
            <div>
              <div class="newsbar-title" v-html="item.title"></div>
              <a class="newsbar-link" :href="item.url" target='_blank'></a>
            </div> 
            <div class="newsbar-datetime" v-html="formatDate(item.datetime)"></div>
            <div class="newsbar-contents" v-html="item.contents"></div>
          </div>
        </div>

        <div class="newsbar-footer">
          <button @click="closeSidebar">Close</button>
        </div>
      </div>
      
      
      <!-- Î¶¨Î∑∞ page -->
      <div class="review" v-else>
        <!-- header -->
        <div class="review-header">
          <div class="review-icon"></div>
          <div class="review-title" v-text="searchText"></div>
        </div>

        <!-- body -->
        <div ref="chatArea" class="review-body">
          <div v-for="(user, index) in reviewContent" :key="index">
            <div class="chatDate">
              <div class="chatDate-content">
                {{ user.chatDate }}
              </div>
            </div>

            <div class="userId">
              {{ user.userID}}
            </div>
            
            <div class="chat" >
              <div class="chatting">
                {{ user.chatting }}
              </div>
              <div class="chatTime">
                {{ user.chatTime }}
              </div>
            </div>
          
          </div>

          <!-- ÎÇ†Ïßú Ï∂úÎ†• -->
          <div class="chatDate">
            <div class="myChatdate" v-if="reviewList.length !== 0" v-text="currentDate"></div>
          </div>

          <!-- ÏãúÍ∞ÑÍ≥º ÎÇ¥Ïö© Ï∂úÎ†• -->
          <div class="myChat-contain" v-if="reviewList.length !== 0">
            <div v-for="(item, index) in reviewList" :key="index" class="myChat">
              <div class="myChattime" v-if="index === reviewList.length - 1 || reviewList[index + 1]?.time !== item.time ">
                {{ item.time }}
              </div>
              <div style="display: flex; flex-direction: column; align-items: flex-end;">
                <div class="myChatting">
                  {{ item.chat }}
                </div>
              </div>
            </div>
          </div>

        </div>
        
        <!-- footer -->
        <div class="review-footer">
          <!-- ÏûÖÎ†•ÌïòÎäî Í≥≥ -->
          <textarea type="text" v-model="reviewText" @keydown.enter="SendReview" ref="textarea" placeholder="Î∞úÏûêÍµ≠ÏùÑ ÎÇ®Í≤®Ï£ºÏÑ∏Ïöî."/>
          <div class="review-closeBtn">
            <button @click="closeSidebar"> Close</button>
          </div>
        </div>
      </div>

    </div>
    
    <!--  Ïπ¥Ïπ¥Ïò§ÎßµÏù¥ Îì§Ïñ¥Í∞à Î∂ÄÎ∂ÑÏùÑ #mapContain ÏòÅÏó≠ÏúºÎ°ú ÏßÄÏ†ï -->
    <div id="mapContain"></div>

  </div>
</template>

<script>
  export default {
    data:() => ({
      map: null,  // map Í∞ùÏ≤¥Î•º Ï¥àÍ∏∞ÌôîÌï¥Ï§¨Îã§
      overlay: null,
      isSidebarOpen: false, //ÏÇ¨Ïù¥Îìú Î∞î Í∏∞Î≥∏ Ïó¥Î¶º ÏÉÅÌÉú(false)
      marker: null,
      customOverlay: null,
      searchText: 'Ïã§ÎØ∏Ïú†ÏõêÏßÄ ÏïàÏ†Ñ',
      searchList: [],   // {}
      reviewText: '',
      reviewList: [],
      menuPage: 'news', // news, review
      reviewContent: [
        {userID: "Ï∞®Ï∞®Ï∞®", chatting: "Ïó¨Î¶ÑÏóê Í∞îÎäîÎç∞ ÏÉ§ÏõåÏã§ÎèÑ ÏûàÍ≥† ÏàòÏòÅÌïòÍ∏∞ÎèÑ Ï¢ãÏïòÏñ¥Ïöî !!", chatDate:"2023 . 7 . 27", chatTime: "11 : 20",},
        {userID: "Ï∞®Î¶∞Ïù¥", chatting: "Ï≤òÏùå Ï∞®Î∞ïÌïòÏãúÎäî Î∂ÑÎì§Íªò Ï∂îÏ≤úÎìúÎ¶ΩÎãàÎã§!", chatTime: "14 : 22",},
        {userID: "Ï∞®Î∞ïÎßàÏä§ÌÑ∞ÍøàÎÇòÎ¨¥", chatting: "Î∞îÎã§ Î≥¥Í≥† Ïã∂ÏúºÏãúÎ©¥ ÏôºÏ™ΩÏúºÎ°ú Îì§Ïñ¥Í∞ÄÎäî Í≤É „Öä„Öä", chatDate:"2023 . 11 . 23", chatTime: "18 : 37",},
        {userID: "Ï∞®Î∞ïÎ∞ïÌïòÏÇ¨ÌÉïÌÉïÌõÑÎ£®", chatting: "Í∑ºÏ≤ò 00ÌöüÏßëÏù¥ Î†àÏ†ÑÎìú ÎßõÏûàÏùå;;", chatDate:"2024 . 10 . 17", chatTime: "15 : 20",},
        {userID: "Ï∞®Î∞ïÌï¥Î≥ºÍπå", chatting: "ÏÉÅÎãπÌûà Ï∂îÏõÄ. Ïô∏Ìà¨ Ï±ôÍ≤®Í∞ÄÏÑ∏Ïöî", chatDate:"2024 . 11 . 16", chatTime: "18 : 33",},
      ],
      currentDate: '',
      currentTime: '',
    }),
    async mounted() {

      this.currentDate = new Date();

      if (window.kakao && window.kakao.maps) {  // Ïπ¥Ïπ¥Ïò§Îßµ ÎùºÏù¥Î∏åÎü¨Î¶¨Í∞Ä Ï°¥Ïû¨Ìï† Îïå,
        this.loadMap();
      } else {  // Ïπ¥Ïπ¥Ïò§, Ïπ¥Ïπ¥Ïò§Îßµ ÎùºÏù¥Î∏åÎü¨Î¶¨Í∞Ä Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏùÑ Îïå,
        this.loadScript();
      }

      // Ïπ¥Ïπ¥Ïò§ Í≤ÄÏÉâ API Î∂àÎü¨Ïò§Í∏∞
      const searchRes = await this.$axios.get(`https://dapi.kakao.com/v2/search/web`, {
          params: { 
            query: this.searchText, 
            sort: 'accuracy',
            page: 1, 
            size: 10, 
          },
          headers: {
            Authorization: 'KakaoAK 6f28d7e14b2449fb380b7a2c0af2d9b4'
          },
        });
        // console.log("searchRes.data.documents:", searchRes.data.documents);

        this.searchList = searchRes.data && searchRes.data.documents ? searchRes.data.documents : [];
    },
    methods: {
      handleEnter() {
      // Enter ÌÇ§ ÏûÖÎ†• Ïãú Ïª§ÏÑú ÏúÑÏπòÎ•º Ïú†ÏßÄ
      const textarea = this.$refs.textarea;
      if (textarea) {
        // Í∞ïÏ†úÎ°ú Ïª§ÏÑúÎ•º ÌÖçÏä§Ìä∏ ÎÅùÏúºÎ°ú Ïù¥Îèô
        const cursorPosition = textarea.selectionEnd;
        textarea.setSelectionRange(cursorPosition, cursorPosition);
      }
      },
      searchAction(e) {
        this.searchText = e.target.value;        
      },
      
      formatDate(datetime) {
        if (!datetime) return '';
          const [date] = datetime.split('T'); 
          return date; 
      },
      loadMap() {   // Ïπ¥Ïπ¥Ïò§ ÎßµÏùÑ Í∑∏Î¶¨Îäî Ìï®Ïàò
        const container = document.getElementById('mapContain');
        const options = {
          center: new window.kakao.maps.LatLng(37.40198, 126.4021), //Ï¢åÌëú(Íµ¨Í∏Ä)
          level: 3,   // ÏßÄÎèÑÏùò ÌôïÎåÄ,Ï∂ïÏÜå Ï†ïÎèÑ
        };        
        this.map = new window.kakao.maps.Map(container, options);
        
        // map ÎÇ¥Î∂ÄÏóê zoomControl Í∏∞Îä• Ï∂îÍ∞Ä 
        var zoomControl = new window.kakao.maps.ZoomControl();
        this.map.addControl(zoomControl, window.kakao.maps.ControlPosition.RIGHT);
        this.loadMarker();
      },
      loadScript() {  // Ïπ¥Ïπ¥Ïò§Îßµ ÎùºÏù¥Î∏åÎü¨Î¶¨Î•º Î°úÎìúÌïúÎã§.
        const script = document.createElement("script");    // <script>< /script>
        script.src = "//dapi.kakao.com/v2/maps/sdk.js?appkey=cdb7bc47b59d2de4ee68f3e460e373cf&autoload=false"; // <script src=""><script>
        script.onload = () => window.kakao.maps.load(this.loadMap); // onclick Ïù¥Î≤§Ìä∏Ï≤òÎüº onload ÎùºÎäî Ïù¥Î≤§Ìä∏Î•º Ïã§ÌñâÏãúÌÇ§Îäî Í≤É(scriptÎ•º Ï§ÄÎπÑÌïòÎäî Ïù¥Î≤§Ìä∏)
        document.head.appendChild(script);  
        // ÏúÑÏóêÏÑú Ï†úÏûëÌï¥Ï§Ä scriptÎ•º html > head ÌÉúÍ∑∏ ÎÇ¥Î∂ÄÏóê Ï∂îÍ∞Ä Ìï¥Ï£ºÎäî Î∂ÄÎ∂Ñ!
        // appendChildÎ•º ÏîÄÏúºÎ°úÏç® head ÌÉúÍ∑∏ Îß® Îí§Ïóê script ÌÉúÍ∑∏Í∞Ä ÏúÑÏπòÌïúÎåú.
      },
      loadMarker() {
        // ÎßàÏª§ Î≥ÄÍ≤Ω 
        const imageSrc = 'https://paperless-dev.bizmeka.com/resources/public/template/_kddx/images/marker.svg' // ÎßàÏª§Ïù¥ÎØ∏ÏßÄÏùò Ï£ºÏÜå
        const imageSize = new window.kakao.maps.Size(35, 35) // ÎßàÏª§Ïù¥ÎØ∏ÏßÄÏùò ÌÅ¨Í∏∞
        const imageOption = {offset: new window.kakao.maps.Point(19, 20)}; // ÎßàÏª§Ïù¥ÎØ∏ÏßÄÏùò ÏòµÏÖò
        const markerImage = new window.kakao.maps.MarkerImage(imageSrc, imageSize, imageOption); // ÎßàÏª§Ïùò Ïù¥ÎØ∏ÏßÄ Ï†ïÎ≥¥Î•º Í∞ÄÏßÄÍ≥† ÏûàÎäî MarkerImage Í∞ùÏ≤¥ ÏÉùÏÑ±
        const markerPosition = new window.kakao.maps.LatLng(37.40198, 126.4021); // ÎßàÏª§ ÏúÑÏπò ÏÑ§Ï†ï

        // ÎßàÏª§ ÏÉùÏÑ±
        this.marker = new window.kakao.maps.Marker({
          position: markerPosition,
          image: markerImage,
        });

        // ÎßàÏª§Î•º ÏßÄÎèÑÏóê ÌëúÏãú
        this.marker.setMap(this.map);
        
        const content = `
        <div class="marker-tooltip">
          <span class="center">Click üëá</span>
        </div>`;

        // Ïª§Ïä§ÌÖÄ Ïò§Î≤ÑÎ†àÏù¥Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
        this.customOverlay = new window.kakao.maps.CustomOverlay({
          position: markerPosition,
          content: content,
        });
          
        this.customOverlay.setMap(this.map);

        // ÎßàÏª§Ïóê ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏Í∞Ä Î∞úÏÉùÌïòÎ©¥ Ïª§Ïä§ÌÖÄ Ïò§Î≤ÑÎ†àÏù¥Î•º ÏßÄÎèÑÏóê Ï†úÍ±∞Ìï©ÎãàÎã§
        window.kakao.maps.event.addListener(this.marker, 'click', () => {
          this.customOverlay.setMap(null);
          document.querySelector('#mapContain >div>div>div:last-of-type>div:first-of-type img').style.animationName='none';
          setTimeout(() => {
            const mkposition = this.marker.getPosition();
            this.map.relayout();
            this.map.setCenter(mkposition);
          },0);
        });

        // Ïª§Ïä§ÌÖÄ Ïò§Î≤ÑÎ†àÏù¥(ÏÇ¨Ïù¥ÎìúÎ∞î) ÏÉùÏÑ±
        this.sidebarOverlay = new window.kakao.maps.CustomOverlay({
          map: this.map,
        });

        // Ïò§Î≤ÑÎ†àÏù¥(ÏÇ¨Ïù¥ÎìúÎ∞î)Î•º Ï≤òÏùåÏóêÎäî Îã´ÏùÄ ÏÉÅÌÉúÎ°ú ÏÑ§Ï†ï
        this.sidebarOverlay.setMap(null);

        // ÎßàÏª§ ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏ Î∞úÏÉù Ïãú ÏÇ¨Ïù¥ÎìúÎ∞î Ï∂úÎ†•
        window.kakao.maps.event.addListener(this.marker, "click", () => {
          this.sidebarOverlay.setMap(this.map);
          this.isSidebarOpen = true;
        });
      },
      closeSidebar() {
        this.isSidebarOpen = false;
        if (this.customOverlay) {
          this.customOverlay.setMap(this.map);
          document.querySelector('#mapContain >div>div>div:last-of-type>div:first-of-type img').style.animationName='tooltip';          
        }
        setTimeout(() => {
          const mkposition = this.marker.getPosition();
          this.map.relayout();
          this.map.setCenter(mkposition);
        },0);
      },
      menuContent(item) {
        this.menuPage = item;

        if (this.menuPage === 'review') {
          this.$nextTick(() => {
            const separateSettingArea = document.querySelector('.review-body');
            const chatArea = this.$refs.chatArea;
            chatArea.scrollTo({ top: separateSettingArea.scrollHeight, behavior: 'smooth', });
          });
        }
      },
      /* Î¶¨Î∑∞Ï∞Ω Ïª®Ìä∏Î°§ */
      SendReview(e) {
        if (e.keyCode === 13 && !e.shiftKey) {
          e.preventDefault(); 

          if (!this.reviewText.trim()){
            return;
          }

          // ÌòÑÏû¨ ÏûÖÎ†•Îêú ÏãúÍ∞ÑÍ≥º Ï±ÑÌåÖ ÎÇ¥Ïö©
          const newMessage = {
            chat: this.reviewText,
            time: this.timeFormat(),
          };

          this.reviewList.push(newMessage);
          this.reviewText = ''; // ÏûÖÎ†•Ï∞Ω Ï¥àÍ∏∞Ìôî

          this.$nextTick(() => {  // DOMÏù¥ Î°úÎî©Ïù¥ ÏôÑÎ£åÎêú ÌõÑ
            this.currentDate = this.dateFormat();
            this.currentTime = this.timeFormat();

            // ÏûêÎèô Ïä§ÌÅ¨Î°§
            const separateSettingArea = document.querySelector('.review-body');
            const chatArea = this.$refs.chatArea;
            chatArea.scrollTo({ top: separateSettingArea.scrollHeight, behavior: 'smooth' });
          });

          // console.log("this.reviewList :: ", this.reviewList);
        }
      },      
      dateFormat() {
        const today = new Date();
        const year = today.getFullYear(); // 2023
        const month = (today.getMonth() + 1).toString().padStart(2, '0'); // 06
        const day = today.getDate().toString().padStart(2, '0'); // 18

        const dateString = year + '.' + month + '.' + day; // 2023-06-18

        return dateString;
      },
      timeFormat(date = new Date()) {
        const hours = date.getHours();
        const minutes = date.getMinutes().toString().padStart(2, '0');

        return hours + ":" + minutes
      },

    },
  };
</script>

<style scoped>
  .menu{
    background-color: white;
    color: #01321f;
    font-weight: 700;
  }
</style>

